/* ==========================================================================
3. Resume container
========================================================================== */
.resume-container {
  position: relative;
  top: -60px;
  width: 95%;
  max-width: 1140px;
  margin: 0 auto;
  padding-top: 130px;
  border-radius: 8px;
  background-color: #fff;
  box-shadow: 0 11px 40px 0 rgba(0, 0, 0, 0.3);
