    margin-bottom: 0;
  }
}
/* ==========================================================================
4. Resume card + Sidenote
========================================================================== */
@media (min-width: 992px) {
  .resume-card,
  .side-note {
    display: inline-block;
    float: none;
    vertical-align: middle;
  }
}
.side-note {
  font-size: 15px;
  line-height: 1.3;
  position: relative;
  text-align: left;
  color: #ff3417;
}
@media (min-width: 992px) {
  .side-note {
    font-size: 16px;
    top: 22px;
    padding-right: 60px;
    text-align: right;
  }
}
.side-note:before,
.side-note:after {
  position: absolute;
  top: 50%;
  display: none;
  content: '';
  transform: translateY(-50%);
  background-color: #ff3417;
}
@media (min-width: 992px) {
  .side-note:before,
  .side-note:after {
    display: block;
  }
}
.side-note:before {
  right: 40px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
}
.side-note:after {
  right: 20px;
  width: 20px;
  height: 1px;
}
.resume-card {
  padding: 30px;
  border-radius: 6px;
  background-color: #fff;
  box-shadow: 0 3px 10px rgba(157, 146, 178, 0.17);
  border: 1px solid rgba(157, 146, 178, 0.2);
