    top: 40px;
    right: 60px;
    float: right;
  }
  .global-info .address-contact p,
  .global-info .address-contact span {
    font-size: 10px !important;
  }
  .global-info .address-contact p {
    margin: 0 !important;
    padding: 0 0 0 15px;
    border-left: 1px solid #000;
  }
  .global-info .name-jobtitle .title {
    font-size: 22px;
  }
  .global-info .address {
    margin-right: 5px;
  }
  .global-info .col-md-5 {
    float: left;
    width: 40%;
  }
  .global-info .col-md-7 {
    display: inline-block !important;
    float: right;
    width: 60%;
  }
  .resume-section {
    margin-top: 40px;
    padding-top: 40px;
    border-top: 1px solid #000;
  }
  .resume-container .category-title {
    top: 0;
    padding: 0;
  }
  .side-note {
    font-size: 14px;
    padding: 0 0 0 0;
    color: #fff;
  }
  .resume-container .category-title h2 {
    margin: 0;
    padding: 0 0 20px 0;
  }
  .resume-container .category-title h2:before {
    display: none;
  }
  .resume-card {
    padding: 16px 30px 30px 30px;
  }
  .resume-card .title {
    padding-top: 15px;
  }
  .card-container {
    top: 0;
    margin-bottom: 40px;
  }
  .card-container:last-child {
    margin-bottom: 0;
  }
  .progress-bar {
    text-align: left;
  }
  .progress span {
    left: 0;
    margin: 0;
    padding: 0;
  }
}
